import "./newUser.css"

export default function NewUser() {
    return (
        <div className="newUser">
            <h1 className="newUserTitle">New User</h1>
            <div className="newUserContainer">
                <form action="" className="newUserForm">
                    <div className="newUserItem">
                        <label>Username</label>
                        <input type="text" className="newUserInput" placeholder="Username"/>
                    </div>
                    <div className="newUserItem">
                        <label>Email</label>
                        <input type="email" className="newUserInput" placeholder="email"/>
                    </div>
                    <div className="newUserItem">
                        <label>Phone</label>
                        <input type="text" className="newUserInput" placeholder="Phone"/>
                    </div>
                    <div className="newUserItem">
                        <label>Full Name</label>
                        <input type="text" className="newUserInput" placeholder="Fullname"/>
                    </div>
                    <div className="newUserItem">
                        <label>Password</label>
                        <input type="text" className="newUserInput" placeholder="Password"/>
                    </div>
                    <div className="newUserItem">
                        <label>Address</label>
                        <input type="text" className="newUserInput" placeholder="Address"/>
                    </div>
                    <div className="newUserItem">
                        <label>Gender</label>
                        <div className="newUserGender">
                            <input type="radio" name="gender" id="male" value="male"/>
                            <label>Male</label>
                            <input type="radio" name="gender" id="female" value="female"/>
                            <label>Female</label>
                            <input type="radio" name="gender" id="other" value="other"/>
                            <label>Other</label>
                        </div>
                    </div>
                    <div className="newUserItem">
                        <label>Active</label>
                        <div className="newUserActive">
                            <input type="radio" name="active" id="activeTrue" value="activeTrue"/>
                            <label>Yes</label>
                            <input type="radio" name="active" id="activeFalse" value="activeFalse"/>
                            <label>No</label>
                        </div>
                    </div>
                    <button className="newUserButton">Create</button>
                </form>
            </div>
        </div>
    )
}
